.product-page {
    header {
        background: $light-grey;
        .padder {
            .banner-text {               
                .padder {
              
                    
                    h1 {
                        font-size: 2.6em;
                    }
                    .button {
                    }   
                    .button-light {                    
                    }
                }
            }     
            .banner-img {
                img {
                    border-radius: 8px;
                }                
            }
        }            
    }
    img {
        max-width: 100%;
    }
    .header-bar {
        background: darken($light-grey, 5%);        
        .padder {
            padding: 0.4em 0;
            @media only screen and (max-width : 1100px) {
                padding: 0.4em 1em;
            } 
            @media only screen 
            and (min-device-width : 320px) 
            and (max-device-width : 480px) {
                padding: 0.4em 1em;
            }
            .header-bar-nav {
                float: left;
                @include phone-reset;
                li {
                    float: left;
                    @include phone-reset;
                    a {
                        &:hover {
                            background: darken($light-grey, 10%);
                        }
                    }
                    .header-bar-item {
                        display: block;
                        text-align: center;
                        padding: 0.4em 1em;
                        border-radius: 2px;
                        margin-right: 0.4em;
                        position: relative;                        
                        @media only screen 
                        and (min-device-width : 320px) 
                        and (max-device-width : 480px) {
                            margin: 0 0 0.4em;
                        }                    
                    }
                    &:last-child {
                        a {
                            margin-right: 0;
                        }
                    }
                }
                .active {
                    a {
                        background: darken($light-grey, 10%);                        
                    }
                }
            }
            .header-button {
                float: right;
                text-align: center;                
                @include phone-reset;
                @media only screen 
                and (min-device-width : 320px) 
                and (max-device-width : 480px) {
                    display: block;
                }            
                .button-inner {
                    padding: 0.4em 1em;
                    border-radius: 2px;
                    .price {
                        background: darken(#4e7979,3%);
                        font-size: 0.6em;
                        padding: 0.4em 1em;
                        position: relative;
                        top: -1px;
                        margin-left: 0.4em;
                        border-radius: 12px;
                        text-transform: uppercase;
                        display: inline-block;
                        vertical-align: middle;
                        color: $light-grey;
                    }                    
                }
                &:hover {
                    .button-inner {
                        
                    }
                }                
            }
            .demo-button {
                .button-inner {
                    margin-right: 0.4em;
                }
                &:hover {
                    .button-inner {
                    }
                }
            }            
            .buynow-button {
                
                .button-inner {
                    background: #645582;
                    margin-left: 0.4em;
                    .price {
                        background: darken(#584b73, 3%);                        
                    }
                }
                &:hover {
                    .button-inner {
                        background: darken(#645582, 3%);
                    }
                }
            }
        }    
    }
    .value-icons {
        li {
            float: left;
            width: 25%;
            text-align: center;
            @include phone-reset;
            .padder {
                padding: 1em 0;
                background: $light-grey;
                margin-right: 1em;
                margin-bottom: 1em;
                @include phone-margin;
                .lnr, i {
                    display: inline-block;
                    font-size: 32px;
                    margin-bottom: 6px;
                }
                h3 {
                    margin: 0;
                    font-size: 1em;
                }
            }
        }
    }
    .alternating-text {
        float: right;
        width: 50%;
        @include phone-reset;
    }
    .alternating-img {
        float: left;
        width: 50%;
        @include phone-reset;
        .padder {
            padding: 0 2em 0 0;
            img {
                width: 100%; 
            }
        }
    } 
    .alternating-right {
        .alternating-text {
            float: left;
            width: 50%;
            @include phone-reset;
        }
        .alternating-img {
            float: right;
            width: 50%;
            @include phone-reset;
            .padder {
                padding: 0 0 0 2em;
                img {
                    width: 100%; 
                }
            }
        }         
    }
    .align-center {
        .section-title {
            text-align: center;
        }
    }
}